import { useState } from "react";
import { useDispatch } from "react-redux";
import { validarEmail } from "../helpers/helper";
// import { deleteUserDB } from "../redux/actions/users_actions"
import { useToasts } from "react-toast-notifications";
import {
  changeCurrentUser,
  consultarUsuario,
} from "../redux/actions/users_actions";

export const useFormBuscarUsuario = ({ initialState = {} }) => {
  const [values, setValues] = useState(initialState);
  const [findValues, setFindValues] = useState({
    campo: "email",
    campoValue: "",
  });
  const dispatch = useDispatch();
  const { addToast } = useToasts();

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 2500,
    });
  };
  const handleInputChange = ({ target }) => {
    setValues({
      ...values,
      [target.name]: target.value,
    });
  };

  const handlerBlurEmail = () => {
    if (validarEmail(values.email)) {
      setValues({
        ...values,
        errorEmail: "",
      });
    } else {
      setValues({
        ...values,
        errorEmail: "El email no es válido",
      });
    }
  };

  const eliminarUsuario = (e, id, email) => {
    e.preventDefault();
    //  //console.log(id);
    //  //console.log(email);
    // dispatch(deleteUserDB(id, email))
  };

  const buscarUsuario = () => {
    if (
      findValues.campo === "" ||
      findValues.campo === undefined ||
      findValues.campoValue === "" ||
      findValues.campoValue === undefined
    ) {
      toastSms("Debes ingresar un valor de busqueda", "error");
    } else {
      if (findValues.campo === "email") {
        if (findValues.campoValue.includes("@")) {
          toastSms(
            "por favor no ingrese @rapifas.com, solo nombre de usuario",
            "info"
          );
        } else {
          dispatch(
            consultarUsuario(
              findValues.campo,
              `${findValues.campoValue}@rapifas.com`,
              toastSms
            )
          );
        }
      } else {
        dispatch(
          consultarUsuario(findValues.campo, findValues.campoValue, toastSms)
        );
      }
    }
  };

  const handlerChangeUser = (user) => {
    dispatch(changeCurrentUser(user));
  };

  const onClickChangeHandler = () => {
    if (
      values.errorMsg === "" &&
      values.errorEmail === "" &&
      values.errorMsg &&
      values.errorEmail
    ) {
      if (
        (values.rol !== "" && values.responsable !== "",
        values.email !== "",
        values.codigoPostal !== "",
        values.ruc !== "",
        values.telefonoEmpresa !== "",
        values.telefonoResponsable !== "",
        values.direccion !== "",
        values.provincia !== "",
        values.canton !== "",
        values.parroquia !== "")
      ) {
        // HACER DISPATCH
        // let dataUser = {
        //     rol: values.rol,
        //     nombre: values.nombre,
        //     email: values.email,
        //     codigoPostal: values.codigoPostal,
        //     dni: values.dni,
        //     telefPrincipal: values.telefPrincipal,
        //     direccion: values.direccion,
        //     telef1: values.telef1,
        //     provincia: values.provincia,
        //     telef2: values.telef2,
        //     canton: values.canton,
        //     parroquia: values.parroquia,
        // }
        //  //console.log("ANTES DE DISPATCH")
        // //  //console.log(dataUser);
        // dispatch(createUser(v, values.password, reset, msgCreateOk, msgCreateError))
      }
    } else {
      // Existen errores
      setValues({
        ...values,
        errorMsg: "Existen Errores en el formulario",
      });
    }
  };
  const handlerInputFind = ({ target }) => {
    //  //console.log("HANDLE INPUT FIND")
    setFindValues({
      ...findValues,
      [target.name]: target.value,
    });
  };

  return [
    values,
    findValues,
    handleInputChange,
    onClickChangeHandler,
    handlerBlurEmail,
    handlerInputFind,
    buscarUsuario,
    handlerChangeUser,
    setValues,
    eliminarUsuario,
  ];
};
