import {
  GET_USERS,
  GET_USERS_ERROR,
  GET_USERS_SUCCESS,
  CURRENT_USER,
  DELETE_USER,
  DELETE_USER_ERROR,
  DELETE_USER_SUCCESS,
} from "../../types/types";
import { auth, db } from "../../firebase";

export const consultarUsuario = (campo, value, smsInfo) => {
  return async (dispatch) => {
    dispatch({
      type: GET_USERS,
    });
    try {
      console.log(campo);
      console.log(value.toLowerCase());
      let userList = [];
      let resp = await db
        .collection("usuarios")
        .where(
          campo,
          "==",
          campo === "responsable" ? value.toUpperCase() : value.toLowerCase()
        )
        .get();
      resp.docs.forEach((element) => {
        let temp = element.data();
        temp.id = element.id;
        userList.push(temp);
      });
      console.log(userList);
      if (userList.length === 0) {
        smsInfo("Usuario no encontrado", "info");
      } else {
        smsInfo("Usuario encontrado", "success");
      }
      dispatch({
        type: GET_USERS_SUCCESS,
        payload: userList,
      });
    } catch (error) {
      dispatch({
        type: GET_USERS_ERROR,
        payload: "Error al Buscar usuario",
      });
      console.log("----- Error de busqueda de usuario -----");
      console.log(error);
      smsInfo("No fue posible encontrar el usuario", "error");
    }
  };
};

