import { truncate } from '../helpers/helper';
import { LOGIN } from '../types/types';
import { listProvincias } from './new_provincia';

export const namesProvincias = (list) => {
	let tempProvincias = [ list[0].provincia ];
	list.map((e) => {
		if (!tempProvincias.includes(e.provincia)) {
			tempProvincias = [ ...tempProvincias, e.provincia ];
		}
	});
	return tempProvincias;
};

export const namesCantones = (selected) => {
	let tempList = [];
	if (selected === undefined || selected === '') {
		return tempList;
	} else {
		let tempProvincias = listProvincias.filter((item) => {
			if (item.provincia.trim() === selected.trim()) {
				return item;
			}
		});
		tempList = [ tempProvincias[0].canton ];

		tempProvincias.map((e) => {
			if (!tempList.includes(e.canton)) {
				tempList = [ ...tempList, e.canton ];
			}
		});
		return tempList;
	}
};

export const namesParroquias = (selected) => {
	let tempList = [];
	if (selected === undefined || selected === '') {
		return tempList;
	} else {
		let tempCantones = listProvincias.filter((item) => {
			if (item.canton.trim() === selected.trim()) {
				return item;
			}
		});
		tempList = [ tempCantones[0].parroquia ];

		tempCantones.map((e) => {
			if (!tempList.includes(e.parroquia)) {
				tempList = [ ...tempList, e.parroquia ];
			}
		});
		return tempList;
	}
};

export const trayectoSelected = (provincia, canton, parroquia) => {
	let tempCantones = [];
	if (
		provincia === '' ||
		provincia === undefined ||
		canton === '' ||
		canton === undefined ||
		parroquia === '' ||
		parroquia === undefined
	) {
		//  //console.log('no seleccionado');
		return;
	} else {
		tempCantones = listProvincias.filter((item) => {
			if (
				item.provincia.trim() === provincia.trim() &&
				item.canton.trim() === canton.trim() &&
				item.parroquia.trim() === parroquia.trim()
			) {
				return item;
			}
		});
		 //console.log(tempCantones[0]);
		return tempCantones[0];
	}
};

export const totalKilo = (valueKilo, totalkilosValue, costoAdicional) => {
	let totalprice;
	if (totalkilosValue === 0 || totalkilosValue === '' || totalkilosValue === undefined) {
		totalprice = 0.0;
	} else {
		//  //console.log('dentro de peso');
		//  //console.log(`total kilos: ${totalkilosValue}`);
		if (parseFloat(totalkilosValue) <= 2.99) {
			//  //console.log('menor a tres kilos');
			totalprice = parseFloat(valueKilo);
		} else {
			 //console.log('mayor a dos kilos');
			let difPeso = parseFloat(totalkilosValue) - 2;
			let totalCalculo = difPeso * truncate(parseFloat(costoAdicional) * 0.12 + parseFloat(costoAdicional), 2);
			totalprice = parseFloat(totalCalculo + valueKilo);
			//  //console.log(`valor por kilo: ${valueKilo}`);
			//  //console.log(`difpeso: ${difPeso}`);
			//  //console.log(
			// 	`costoAdicional por quilo: ${truncate(
			// 		parseFloat(costoAdicional) * 0.12 + parseFloat(costoAdicional),
			// 		2
			// 	)}`
			// );
			//  //console.log(`costo adicional: ${totalCalculo}`);
			//  //console.log(`totalprice: ${totalprice}`);
		}
	}
	//  //console.log('-----------values --------------');
	//  //console.log(totalprice);
	//  //console.log('-----------values --------------');
	return parseFloat(truncate(totalprice, 2));
};
