import React, { useEffect } from "react";
// import Footer from "../../../comonents/footer/footer";
import "../dashboard_home/dashHome.css";
import logo from "../../../img/new_logo.png";
import logoId from "../../../img/idCodelogoPrint.png";

function DashHome() {
  const ID = "{ID}";

  // useEffect(() => {
  //   // //  //console.log("===> DESDE DASH HOME");
  // }, [])
  return (
    <div className="col-md-9 ms-sm-auto col-lg-10  h-100 home-section">
      <img
        className="img-logo-id m-4 position-absolute bottom-0 end-0 "
        src={logoId}
        alt="logo"
      />
      <div
        className="row align-items-center justify-content-center section-welcome"
        //   style="height: 350px"
      >
        <div className="col text-start p-4">
          <div>
            {/* <h1 className="title-item">IRIS</h1> */}
            <img className="img-logo pb-2" src={logo} alt="logo" />

            <hr />
            <ul>
              <li className="tiems-style">Servicio de Correos</li>
              <li className="tiems-style">Servicios Postales</li>
              <li className="tiems-style">Respaldo y Control Postal</li>
              <li className="tiems-style">Logística y Operaciones</li>
            </ul>
          </div>
          <div className="btn btn-primary">
            <a className="linkCOlor" href="/">
              
            <div className="row">
              <i class="material-icons col-1 px-2">keyboard_arrow_left</i>
              <div className="col-11">Volver a la Página Principal</div>
            </div>
            </a>
          </div>
        </div>
        <div className="col bd-highlight ">
          <img
            src="https://i.imgur.com/9pK7vVE.png"
            className="img-fluid"
            alt=""
          />
        </div>
        <span className="text-muted fixed-bottom col-md-9 ms-sm-auto col-lg-10 mb-4">
          © Copyright <b>{ID} - CODE</b>. All Rights Reserved Designed by{" "}
          <b>{ID} - CODE</b>
        </span>
      </div>
    </div>
  );
}

export default DashHome;
