import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { validarEmail } from "../helpers/helper";
import { createUser } from "../redux/actions/auth_actions";
import { useToasts } from "react-toast-notifications";

export const useFormCreateUser = (initialState = {}) => {
  const [values, setValues] = useState(initialState);
  const dispatch = useDispatch();
  const { addToast } = useToasts();

  const reset = () => {
    setValues(initialState);
  };

  const msgCreateOk = () =>
    addToast("Usuario creado con éxito.", {
      appearance: "success",
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });

  const msgCreateError = (smsError) => {
    return addToast(` ${smsError}`, {
      appearance: "error",
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };

  const handleInputChange = ({ target }) => {
    setValues({
      ...values,
      [target.name]: target.value,
    });
  };
  const handlerBlur = () => {
    if (values.password !== values.repassword) {
      setValues({
        ...values,
        errorMsg: "Las contraseñas no coinciden*",
      });
    } else {
      setValues({
        ...values,
        errorMsg: "",
      });
    }
  };
  const handlerBlurEmail = () => {
    //  //console.log("ANTES DE COMPROBAR");
    setValues({
      ...values,
      errorEmail: "",
    });
  };
  const onClickChangeHandler = (e) => {
    //console.log("debtri de handler");
    if (e.target.value !== "") {
      e.preventDefault();
    }
    if (values.rol === "") {
      msgCreateError("Primero debes seleccionar un rol");
    } else {
      if (values.errorMsg === "" && values.errorEmail === "") {
        if (
          (values.rol !== "" && values.razonSocial !== "",
          values.ruc !== "",
          values.direccion !== "",
          values.referencia !== "",
          values.provincia !== "",
          values.canton !== "",
          values.parroquia !== "",
          values.responsable !== "",
          values.telefonoEmpresa !== "",
          values.telefonoResponsable !== "",
          values.codigoPostal !== "",
          values.email !== "",
          values.password !== "")
        ) {
          if (values?.email.includes("@")) {
            setValues({
              ...values,
              errorMsg:
                "el email es incorrecto, solo ingrese su nombre de usuario",
            });
            msgCreateError(
              "el email es incorrecto, solo ingrese su nombre de usuario"
            );
          } else {
            let dataUser = {
              rol: values.rol,
              razonSocial: values.razonSocial.toUpperCase(),
              ruc: values.ruc,
              direccion: values.direccion,
              referencia: values.referencia,
              provincia: values.provincia.toUpperCase(),
              canton: values.canton.toUpperCase(),
              parroquia: values.parroquia.toUpperCase(),
              responsable: values.responsable.toUpperCase(),
              telefonoEmpresa: values.telefonoEmpresa,
              telefonoResponsable: values.telefonoResponsable,
              codigoPostal: values.codigoPostal,
              email: `${values.email}@rapifas.com`,
            };
            dispatch(
              createUser(
                dataUser,
                values.password,
                reset,
                msgCreateOk,
                msgCreateError
              )
            );
          }
        }
      } else {
        setValues({
          ...values,
          errorMsg: "Existen Errores en el formulario",
        });
        msgCreateError("Existen campos faltantes en el formulario de registro");
      }
    }
  };
  const test = () => {
    console.log("entro");

    // dispatch(
    //   createUser(
    //     // dataUser,
    //     // values.password,
    //     // reset,
    //     // msgCreateOk,
    //     // msgCreateError
    //   )
    // );
  };
  return [
    values,
    handleInputChange,
    handlerBlur,
    onClickChangeHandler,
    handlerBlurEmail,
    test,
  ];
};
