import React from "react";
import { localidadesList } from "../../../../constants/db_provincias";
import { generateId, ParseDate } from "../../../../helpers/helper";
import "../crear-manifiestos/manifiestos.css";
import { v1 as uuid } from "uuid";
import CrearManifiestoHook from "../../../../hooks/crear_manifiestos_hook";
import { DebounceInput } from "react-debounce-input";
import Spinner from "../../../spinner/spinner";
import EstructuraManifiesto from "../../../dash-componets/estructura_manifiesto/estructura-manifiesto";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
import { namesProvincias } from "../../../../constants/helper_provincias";
import { listProvincias } from "../../../../constants/new_provincia";

function crearManifiesto() {
  const [
    autoSearchGuias,
    setDestinoReducer,
    deleteGuia,
    GuardarManifiesto,
    generateManifiesto,
    changeSearch,
    manualSearcGuias,
    showErrorDestino,
    provinciaDestino,
    listGuiasManifiesto,
    fetching,
    manifiestoPrint,
    autosearchGuiaManifieto,
  ] = CrearManifiestoHook();

  let manifiestotemp = generateManifiesto();
  // let guias = [
  //maximo 35 guias

  // ];
  return (
    <div>
      <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section d-print-none">
        <div className="container-fluid pt-4 ">
          <h4 className="title-separtor">Crear Manifiestos</h4>
          <hr />
          <div className="card card-body">
            <div className="row mt-2">
              <div className="col-lg-6 ">
                <form>
                  <div className="mb-3">
                    <label className="form-label-input">Provincia *</label>
                    <select
                      autoComplete="off"
                      className="form-select"
                      value={provinciaDestino}
                      onChange={setDestinoReducer}
                      name="destinatario provincia"
                      required
                      disabled={listGuiasManifiesto.length === 0 ? false : true}
                    >
                      <option key={`#1_destinatario`} value="">
                        Seleccionar la Provincia
                      </option>
                      {namesProvincias(listProvincias).map((e) => (
                        <option key={`${e}_provincia`} value={e}>
                          {e}
                        </option>
                      ))}
                    </select>
                  </div>
                </form>
              </div>
              <div className="col-lg-6 ">
                <form>
                  <div className="input-group">
                    <label className="form-label-input mb-2">Buscar Guía</label>

                    <DebounceInput
                      minLength={9}
                      debounceTimeout={500}
                      type="text"
                      className="form-control dropdown-toggle"
                      placeholder="Num. Guia..."
                      id="searchGuia"
                      onChange={(e) =>
                        autoSearchGuias(e, autosearchGuiaManifieto)
                      }
                      name="searchText"
                      readOnly={
                        provinciaDestino === "" ||
                        provinciaDestino === undefined
                          ? true
                          : false
                      }
                    />

                    {fetching ? (
                      <div className="container-spinner-buscar">
                        <Spinner />
                      </div>
                    ) : autosearchGuiaManifieto ? (
                      <></>
                    ) : (
                      <button
                        className="btn-solid-secondary px-4 "
                        type="submit"
                        onClick={manualSearcGuias}
                        disabled={
                          provinciaDestino === "" ||
                          provinciaDestino === undefined
                            ? true
                            : false
                        }
                      >
                        Buscar
                      </button>
                    )}
                  </div>
                  <div className="col text-start mt-2">
                    <div className="form-check form-switch">
                      Búsqueda Automática
                      <input
                        className="form-check-input"
                        type="checkbox"
                        id="flexSwitchCheckChecked"
                        checked={autosearchGuiaManifieto}
                        onClick={changeSearch}
                      />
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
          <div className="card card-body">
            <div className="row align-items-center">
              <div className="col-sm-12 col-md-6">
                <h5 className="titleSection-form">Lista de Guias</h5>
              </div>
              <div className="col-sm-12 col-md-6">
                {listGuiasManifiesto.length === 0 &&
                listGuiasManifiesto !== undefined ? (
                  <></>
                ) : (
                  <h5 className="titleSection-form text-end">
                    Num. Guías:{" "}
                    <span className="badge bg-secondary counter">
                      {listGuiasManifiesto !== undefined
                        ? listGuiasManifiesto.length
                        : 0}
                    </span>
                  </h5>
                )}
              </div>
            </div>
            <hr />
            <div className="table-cm table-responsive">
              <table className="table nowrap">
                <thead>
                  <tr className="header-table">
                    <th></th>
                    <th
                      className="text-nowrap"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Num. Guía "
                    >
                      Num. Guía
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Fecha"
                    >
                      Fecha
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Origen"
                    >
                      Origen
                    </th>
                    <th>Destino</th>
                    <th>Estado</th>
                    <th
                      className="text-nowrap"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Eliminar"
                    >
                      Eliminar
                    </th>
                    {/* <th /> */}
                  </tr>
                </thead>

                <tbody>
                  {listGuiasManifiesto?.map((guia, index) => {
                    return (
                      <tr key={`origen_${guia.idGuia}`} className="hover-table">
                        <td className="text-nowrap">
                          <b>{index + 1}</b>
                        </td>
                        <td
                          className="text-nowrap"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={guia.idGuia}
                        >
                          {guia.idGuia}
                        </td>
                        <td
                          className="text-truncate "
                          data-toggle="tooltip"
                          data-placement="top"
                          title={ParseDate(guia?.creacion?.fecha)}
                        >
                          {ParseDate(guia?.creacion?.fecha)}
                        </td>
                        <td
                          className="text-truncate text-nowrap"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={`${guia.remitente.provincia}-${guia.remitente.canton}`}
                        >
                          {`${guia.remitente.provincia}-${guia.remitente.canton}`}
                        </td>
                        <td
                          className="text-truncate text-nowrap"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={`${guia.destinatario.provincia}-${guia.destinatario.canton}`}
                        >
                          {`${guia.destinatario.provincia}-${guia.destinatario.canton}`}
                        </td>
                        <td
                          className="text-nowrap"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={guia?.estado?.toString().toUpperCase()}
                        >
                          {guia?.estado?.toString().toUpperCase()}
                        </td>
                        <td
                          className="text-center"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Eliminar"
                        >
                          <div
                            type="button"
                            className=" delete"
                            onClick={() => deleteGuia(guia)}
                          >
                            <i className="large material-icons delete">
                              delete_forever
                            </i>
                          </div>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
              {listGuiasManifiesto.length === 0 &&
              listGuiasManifiesto !== undefined ? (
                <div className="no-Item">
                  <p className="text-muted noGuias">No hay Guias Registradas</p>
                </div>
              ) : (
                <></>
              )}
            </div>
          </div>

          <div className="card card-body">
            <div className="row">
              {listGuiasManifiesto.length === 0 ? (
                <div
                  className="btn btn-success-disable col mx-1"
                  // type="button"
                >
                  Generar Manifiesto
                </div>
              ) : fetching ? (
                <div className="container-spinner-buscar col mx-1 w-100">
                  <Spinner />
                </div>
              ) : (
                <button
                  className="btn btn-success col mx-1"
                  type="button"
                  onClick={GuardarManifiesto}
                >
                  Generar Manifiesto
                </button>
              )}
            </div>
          </div>
        </div>
        <Dashfooter />
      </div>

      {/* <div className="col-md-9 ms-sm-auto col-lg-10 d-none d-print-block"> */}
      <div className="col-md-9 ms-sm-auto col-lg-10 d-none d-print-block">
        {
          <EstructuraManifiesto
            manifiesto={
              manifiestoPrint?.idManifiesto === "" ||
              manifiestoPrint?.idManifiesto === undefined
                ? manifiestotemp
                : manifiestoPrint
            }
            last={true}
          />
        }
      </div>
    </div>
  );
}

export default crearManifiesto;
