import React, { useState } from "react";
import { ParseDate } from "../../../../helpers/helper";

function TableStatus({ CurrentGuia: currentGuia = {} }) {
  let [statusInfo, setStatusInfo] = useState({});

  const setInfo = (data, tipo) => {
    if (data !== undefined) {
      let datatemp = {
        items: data,
        type: tipo,
      };
      setStatusInfo(datatemp);
      //console.log(datatemp);
    } else {
      setStatusInfo({});
    }
  };

  const titleReturn = (string) => {
    switch (string) {
      case "creacion":
        return "Guia Creada";
      case "updatePeso":
        return "Actualizaciones de Peso";
      case "addManifiesto":
        return "Agregado a un Manifiesto";
      case "receptadoManifiesto":
        return "Receptado en un Manifiesto";
      case "asignada":
        return "Guia Asignada a Cartero";
      case "registroCorrespondencia":
        return "Registro de Entrega Correspondencia";
      default:
        return "";
    }
  };
  return (
    <div>
      <div className="card card-body">
        <div className="col-sm-12 col-md-6">
          <h5 className="titleSection-form">Estados</h5>
        </div>
        <hr />
        <div className="row align-items-start">
          <div className="row table-cm table-responsive">
            <table className="table  nowrap">
              <thead>
                <tr className="header-table">
                  <th>Estado</th>
                  <th
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title="Estado"
                  >
                    Fecha
                  </th>
                  <th
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title="Rol"
                  >
                    Rol
                  </th>
                  <th
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title="Origen"
                  >
                    Usuario
                  </th>
                  <th
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title="Destino"
                  >
                    Detalle
                  </th>
                </tr>
              </thead>

              <tbody>
                <tr key={1} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={"CREADO"}
                  >
                    CREADO
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.creacion?.fecha === undefined
                        ? "------------"
                        : ParseDate(currentGuia?.creacion?.fecha)
                    }
                  >
                    {currentGuia?.creacion?.fecha === undefined
                      ? "------------"
                      : ParseDate(currentGuia?.creacion?.fecha)}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={currentGuia?.creacion?.rol ?? "------------"}
                  >
                    {currentGuia?.creacion?.rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={currentGuia?.creacion?.usuario ?? "------------"}
                  >
                    {currentGuia?.creacion?.usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Más Detalles"
                      onClick={() =>
                        setInfo([currentGuia?.creacion], "creacion")
                      }
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
                <tr key={2} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={"CREADO"}
                  >
                    PESO ACTUALIZADO
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.updatePeso === undefined
                        ? "-------"
                        : currentGuia?.updatePeso[
                            currentGuia?.updatePeso.length - 1
                          ]?.fecha === undefined
                        ? "------------"
                        : ParseDate(
                            currentGuia?.updatePeso[
                              currentGuia?.updatePeso.length - 1
                            ]?.fecha ?? Date.now()
                          )
                    }
                  >
                    {currentGuia?.updatePeso === undefined
                      ? "-------"
                      : currentGuia?.updatePeso[
                          currentGuia?.updatePeso.length - 1
                        ]?.fecha === undefined
                      ? ""
                      : ParseDate(
                          currentGuia?.updatePeso[
                            currentGuia?.updatePeso.length - 1
                          ]?.fecha ?? Date.now()
                        )}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.updatePeso === undefined
                        ? "-------"
                        : currentGuia?.updatePeso[
                            currentGuia?.updatePeso.length - 1
                          ]?.rol ?? "------------"
                    }
                  >
                    {currentGuia?.updatePeso === undefined
                      ? "-------"
                      : currentGuia?.updatePeso[
                          currentGuia?.updatePeso.length - 1
                        ]?.rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.updatePeso === undefined
                        ? "-------"
                        : currentGuia?.updatePeso[
                            currentGuia?.updatePeso.length - 1
                          ]?.usuario ?? "------------"
                    }
                  >
                    {currentGuia?.updatePeso === undefined
                      ? "-------"
                      : currentGuia?.updatePeso[
                          currentGuia?.updatePeso.length - 1
                        ]?.usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Más Detalles"
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                      onClick={() =>
                        setInfo(currentGuia?.updatePeso, "updatePeso")
                      }
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
                <tr key={3} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={"CREADO"}
                  >
                    AGREGADO A MANIFIESTO
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.addManifiesto === undefined
                        ? "---------"
                        : currentGuia?.addManifiesto[
                            currentGuia?.addManifiesto?.length - 1
                          ]?.fecha === undefined
                        ? "------------"
                        : ParseDate(
                            currentGuia?.addManifiesto[
                              currentGuia?.addManifiesto.length - 1
                            ]?.fecha ?? Date.now()
                          )
                    }
                  >
                    {currentGuia?.addManifiesto === undefined
                      ? "---------"
                      : currentGuia?.addManifiesto[
                          currentGuia?.addManifiesto.length - 1
                        ]?.fecha === undefined
                      ? "------------"
                      : ParseDate(
                          currentGuia?.addManifiesto[
                            currentGuia?.addManifiesto.length - 1
                          ]?.fecha ?? Date.now()
                        )}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.addManifiesto === undefined
                        ? "---------"
                        : currentGuia?.addManifiesto[
                            currentGuia?.addManifiesto.length - 1
                          ]?.rol ?? "------------"
                    }
                  >
                    {currentGuia?.addManifiesto === undefined
                      ? "---------"
                      : currentGuia?.addManifiesto[
                          currentGuia?.addManifiesto.length - 1
                        ]?.rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.addManifiesto === undefined
                        ? "---------"
                        : currentGuia?.addManifiesto[
                            currentGuia?.addManifiesto.length - 1
                          ]?.usuario ?? "------------"
                    }
                  >
                    {currentGuia?.addManifiesto === undefined
                      ? "---------"
                      : currentGuia?.addManifiesto[
                          currentGuia?.addManifiesto.length - 1
                        ]?.usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Más Detalles"
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                      onClick={() =>
                        setInfo(currentGuia?.addManifiesto, "addManifiesto")
                      }
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
                <tr key={4} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={"CREADO"}
                  >
                    RECEPCIÓN DE MANIFIESTO
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.receptadoManifiesto === undefined
                        ? "---------"
                        : currentGuia?.receptadoManifiesto[
                            currentGuia.receptadoManifiesto.length - 1
                          ]?.fecha === undefined
                        ? "------------"
                        : ParseDate(
                            currentGuia?.receptadoManifiesto[
                              currentGuia.receptadoManifiesto.length - 1
                            ]?.fecha ?? Date.now()
                          )
                    }
                  >
                    {currentGuia?.receptadoManifiesto === undefined
                      ? "---------"
                      : currentGuia?.receptadoManifiesto[
                          currentGuia.receptadoManifiesto.length - 1
                        ]?.fecha === undefined
                      ? "------------"
                      : ParseDate(
                          currentGuia?.receptadoManifiesto[
                            currentGuia.receptadoManifiesto.length - 1
                          ]?.fecha ?? Date.now()
                        )}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.receptadoManifiesto === undefined
                        ? "---------"
                        : currentGuia?.receptadoManifiesto[
                            currentGuia.receptadoManifiesto.length - 1
                          ]?.rol ?? "------------"
                    }
                  >
                    {currentGuia?.receptadoManifiesto === undefined
                      ? "---------"
                      : currentGuia?.receptadoManifiesto[
                          currentGuia.receptadoManifiesto.length - 1
                        ]?.rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.receptadoManifiesto === undefined
                        ? "---------"
                        : currentGuia?.receptadoManifiesto[
                            currentGuia.receptadoManifiesto.length - 1
                          ]?.usuario ?? "------------"
                    }
                  >
                    {currentGuia?.receptadoManifiesto === undefined
                      ? "---------"
                      : currentGuia?.receptadoManifiesto[
                          currentGuia.receptadoManifiesto.length - 1
                        ]?.usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Más Detalles"
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                      onClick={() =>
                        setInfo(
                          currentGuia?.receptadoManifiesto,
                          "receptadoManifiesto"
                        )
                      }
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
                <tr key={5} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={"CREADO"}
                  >
                    ASIGNADO A CARTERO
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.asignada === undefined
                        ? "---------"
                        : currentGuia?.asignada[
                            currentGuia?.asignada.length - 1
                          ]?.fecha === undefined
                        ? "------------"
                        : ParseDate(
                            currentGuia?.asignada[
                              currentGuia?.asignada.length - 1
                            ]?.fecha ?? Date.now()
                          )
                    }
                  >
                    {currentGuia?.asignada === undefined
                      ? "---------"
                      : currentGuia?.asignada[currentGuia?.asignada.length - 1]
                          ?.fecha === undefined
                      ? "------------"
                      : ParseDate(
                          currentGuia?.asignada[
                            currentGuia?.asignada.length - 1
                          ]?.fecha ?? Date.now()
                        )}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.asignada === undefined
                        ? "---------"
                        : currentGuia?.asignada[
                            currentGuia?.asignada.length - 1
                          ]?.rol ?? "------------"
                    }
                  >
                    {currentGuia?.asignada === undefined
                      ? "---------"
                      : currentGuia?.asignada[currentGuia?.asignada.length - 1]
                          ?.rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.asignada === undefined
                        ? "---------"
                        : currentGuia?.asignada[
                            currentGuia?.asignada.length - 1
                          ]?.usuario ?? "------------"
                    }
                  >
                    {currentGuia?.asignada === undefined
                      ? "---------"
                      : currentGuia?.asignada[currentGuia?.asignada.length - 1]
                          ?.usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Más Detalles"
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                      onClick={() => setInfo(currentGuia?.asignada, "asignada")}
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
                <tr key={6} className="hover-table">
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.registroCorrespondencia === undefined
                        ? "---------"
                        : currentGuia?.registroCorrespondencia[
                            currentGuia?.registroCorrespondencia.length - 1
                          ]?.novedad ?? "------------"
                    }
                  >
                    <b> Entrega:</b>{" "}
                    {currentGuia?.registroCorrespondencia === undefined
                      ? "---------"
                      : currentGuia?.registroCorrespondencia[
                          currentGuia?.registroCorrespondencia.length - 1
                        ].novedad ?? "--- ENTREGA ---"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.registroCorrespondencia === undefined
                        ? "---------"
                        : currentGuia?.registroCorrespondencia[
                            currentGuia?.registroCorrespondencia.length - 1
                          ].fecha === undefined
                        ? "------------"
                        : currentGuia?.registroCorrespondencia[
                            currentGuia?.registroCorrespondencia.length - 1
                          ].fecha +
                            "-" +
                            currentGuia?.registroCorrespondencia[
                              currentGuia?.registroCorrespondencia.length - 1
                            ].hora ?? Date.now()
                    }
                  >
                    {currentGuia?.registroCorrespondencia === undefined
                      ? "---------"
                      : currentGuia?.registroCorrespondencia[
                          currentGuia?.registroCorrespondencia.length - 1
                        ].fecha === undefined
                      ? "------------"
                      : currentGuia?.registroCorrespondencia[
                          currentGuia?.registroCorrespondencia.length - 1
                        ].fecha +
                          "-" +
                          currentGuia?.registroCorrespondencia[
                            currentGuia?.registroCorrespondencia.length - 1
                          ].hora ?? Date.now()}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.registroCorrespondencia === undefined
                        ? "---------"
                        : currentGuia?.registroCorrespondencia[
                            currentGuia?.registroCorrespondencia.length - 1
                          ].rol ?? "------------"
                    }
                  >
                    {currentGuia?.registroCorrespondencia === undefined
                      ? "---------"
                      : currentGuia?.registroCorrespondencia[
                          currentGuia?.registroCorrespondencia.length - 1
                        ].rol ?? "------------"}
                  </td>
                  <td
                    className="text-truncate"
                    data-toggle="tooltip"
                    data-placement="top"
                    title={
                      currentGuia?.registroCorrespondencia === undefined
                        ? "---------"
                        : currentGuia?.registroCorrespondencia[
                            currentGuia?.registroCorrespondencia.length - 1
                          ].usuario ?? "------------"
                    }
                  >
                    {currentGuia?.registroCorrespondencia === undefined
                      ? "---------"
                      : currentGuia?.registroCorrespondencia[
                          currentGuia?.registroCorrespondencia.length - 1
                        ].usuario ?? "------------"}
                  </td>
                  <td className="text-truncate">
                    <a
                      className="actions print"
                      type="button"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Más Detalles"
                      data-bs-toggle="modal"
                      data-bs-target="#detail_status"
                      onClick={() =>
                        setInfo(
                          currentGuia?.registroCorrespondencia,
                          "registroCorrespondencia"
                        )
                      }
                    >
                      <i className="material-icons"> visibility</i>
                    </a>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div
        className="modal fade"
        id="detail_status"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div
          className={
            statusInfo?.type === "registroCorrespondencia"
              ? "modal-dialog modal-dialog-centered modal-fullscreen"
              : "modal-dialog modal-dialog-centered modal-xl"
          }
        >
          <div className="modal-content">
            <div className="modal-header">
              <h5
                className="modal-title text-upercase"
                id="staticBackdropLabel"
              >
                Detalles de Estado - {titleReturn(statusInfo?.type)}
              </h5>

              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="text-start p-4">
              <div>
                <table className="table  nowrap">
                  <thead>
                    <tr className="header-table">
                      <th
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title="Fecha"
                      >
                        Fecha
                      </th>
                      {statusInfo?.type === "registroCorrespondencia" ? (
                        <th
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Hora"
                        >
                          Hora
                        </th>
                      ) : (
                        <></>
                      )}
                      <th
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title="Usuario"
                      >
                        Usuario
                      </th>
                      <th
                        className="text-truncate"
                        data-toggle="tooltip"
                        data-placement="top"
                        title="Rol"
                      >
                        Rol
                      </th>
                      {statusInfo?.type === "updatePeso" ? (
                        <th
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Detalle"
                        >
                          Detalle
                        </th>
                      ) : (
                        <></>
                      )}
                      {statusInfo?.type === "addManifiesto" ? (
                        <th
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Manifiesto"
                        >
                          Manifiesto
                        </th>
                      ) : (
                        <></>
                      )}
                      {statusInfo?.type === "receptadoManifiesto" ? (
                        <th
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Provincia"
                        >
                          Provincia
                        </th>
                      ) : (
                        <></>
                      )}
                      {statusInfo?.type === "asignada" ? (
                        <th
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Nombre Cartero"
                        >
                          Nombre Cartero
                        </th>
                      ) : (
                        <></>
                      )}
                      {statusInfo?.type === "receptadoManifiesto" ||
                      statusInfo?.type === "asignada" ? (
                        <th
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Detalle"
                        >
                          Detalle
                        </th>
                      ) : (
                        <></>
                      )}
                      {statusInfo?.type === "registroCorrespondencia" ? (
                        <th
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Resibido"
                        >
                          Resibido Por
                        </th>
                      ) : (
                        <></>
                      )}
                      {statusInfo?.type === "registroCorrespondencia" ? (
                        <th
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Ruc"
                        >
                          Ruc
                        </th>
                      ) : (
                        <></>
                      )}
                      {statusInfo?.type === "registroCorrespondencia" ? (
                        <th
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Novedad"
                        >
                          Novedad
                        </th>
                      ) : (
                        <></>
                      )}
                      {statusInfo?.type === "registroCorrespondencia" ? (
                        <th
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Descripcion Novedad"
                        >
                          Descripcion Novedad
                        </th>
                      ) : (
                        <></>
                      )}
                      {statusInfo?.type === "addManifiesto" ? (
                        <th
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title="Manifiesto"
                        >
                          Destino
                        </th>
                      ) : (
                        <></>
                      )}
                    </tr>
                  </thead>
                  <tbody>
                    {statusInfo?.items?.map((item, index) => {
                      return (
                        <tr key={1} className="hover-table">
                          {statusInfo?.type === "registroCorrespondencia" ? (
                            <td
                              data-toggle="tooltip"
                              data-placement="top"
                              title={item?.fecha ?? "------------"}
                            >
                              {item?.fecha ?? "------------"}
                            </td>
                          ) : (
                            <td
                              className="text-truncate"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={ParseDate(item?.fecha) ?? "------------"}
                            >
                              {ParseDate(item?.fecha) ?? "------------"}
                            </td>
                          )}
                          {statusInfo?.type === "registroCorrespondencia" ? (
                            <td
                              data-toggle="tooltip"
                              data-placement="top"
                              title={item?.hora ?? "------------"}
                            >
                              {item?.hora ?? "------------"}
                            </td>
                          ) : (
                            <></>
                          )}
                          <td
                            data-toggle="tooltip"
                            data-placement="top"
                            title={item?.usuario ?? "------------"}
                          >
                            {item?.usuario ?? "------------"}
                          </td>
                          <td
                            className="text-truncate"
                            data-toggle="tooltip"
                            data-placement="top"
                            title={item?.rol ?? "------------"}
                          >
                            {item?.rol ?? "------------"}
                          </td>
                          {statusInfo?.type === "updatePeso" ? (
                            <td
                              className="text-truncate"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={item?.detalle ?? "------------"}
                            >
                              {item?.detalle ?? "------------"}
                            </td>
                          ) : (
                            <></>
                          )}
                          {statusInfo?.type === "addManifiesto" ? (
                            <td
                              className="text-truncate"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={
                                currentGuia?.idManifiestos ?? "------------"
                              }
                            >
                              <b>
                                {currentGuia?.idManifiestos[index] ??
                                  "------------"}
                              </b>
                            </td>
                          ) : (
                            <></>
                          )}
                          {statusInfo?.type === "receptadoManifiesto" ? (
                            <td
                              className="text-truncate"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={item?.provincia ?? "------------"}
                            >
                              {item?.provincia ?? "------------"}
                            </td>
                          ) : (
                            <></>
                          )}
                          {statusInfo?.type === "asignada" ? (
                            <td
                              className="text-truncate"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={item?.nomCartero ?? "------------"}
                            >
                              {item?.nomCartero ?? "------------"}
                            </td>
                          ) : (
                            <></>
                          )}
                          {statusInfo?.type === "receptadoManifiesto" ||
                          statusInfo?.type === "asignada" ? (
                            <td
                              className="text-truncate"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={item?.detalle ?? "------------"}
                            >
                              {item?.detalle ?? "------------"}
                            </td>
                          ) : (
                            <></>
                          )}
                          {statusInfo?.type === "registroCorrespondencia" ? (
                            <td
                              className="text-truncate"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={item?.resibido ?? "------------"}
                            >
                              {item?.resibido ?? "------------"}
                            </td>
                          ) : (
                            <></>
                          )}
                          {statusInfo?.type === "registroCorrespondencia" ? (
                            <td
                              className="text-truncate"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={item?.ruc ?? "------------"}
                            >
                              {item?.ruc ?? "------------"}
                            </td>
                          ) : (
                            <></>
                          )}
                          {statusInfo?.type === "registroCorrespondencia" ? (
                            <td
                              className="text-truncate"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={item?.novedad ?? "------------"}
                            >
                              {item?.novedad ?? "------------"}
                            </td>
                          ) : (
                            <></>
                          )}
                          {statusInfo?.type === "registroCorrespondencia" ? (
                            <td
                              className="text-break size-description"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={
                                item?.descripcion_novedad ?? "------------"
                              }
                            >
                              {item?.descripcion_novedad ?? "------------"}
                            </td>
                          ) : (
                            <></>
                          )}
                          {statusInfo?.type === "addManifiesto" ? (
                            <td
                              className="text-truncate"
                              data-toggle="tooltip"
                              data-placement="top"
                              title={item?.destino ?? "------------"}
                            >
                              {item?.destino ?? "------------"}
                            </td>
                          ) : (
                            <></>
                          )}
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
            <div className="modal-footer">
              <button
                type="submit"
                className=" btn btn-success p-2"
                data-bs-dismiss="modal"
              >
                Aceptar
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TableStatus;
