import "../buscar_guias/buscar_guias.css";
import React, { useState, Component } from "react";
import {
  nameRolUser,
  ParseDate,
  removeEmail,
  returnTotalGuias,
  returnTotalValorAsegurado,
  savePDF,
} from "../../../../helpers/helper";
import {
  generateGuiatosearch,
  generateGuiatosearchtoPrint,
  generateListGuias,
} from "../../../../helpers/estructura-pdf";
import Spinner from "../../../spinner/spinner";
import Dashfooter from "../../../dash-componets/footter/dashfooter";
import { UseFormDardeBajaGuias } from "../../../../hooks/user_from_buscar_guias_dardebaja";
import EstructuraGuia from "../../../estructura-guia-imprimir/estructura-print";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { DebounceInput } from "react-debounce-input";
import TableStatus from "./table_status";
import { namesProvincias } from "../../../../constants/helper_provincias";
import { listProvincias } from "../../../../constants/new_provincia";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";

function BuscarGuias() {
  const [
    values,
    fetching,
    listGuias,
    currentGuia,
    rolUser,
    listusernames,
    listFilterUserNames,
    autosearchBuscarGuias,
    busquedapersonalizada,
    onclickSwitch,
    setAdvanceSearch,
    darDeBaja,
    getGuiaSearchAuto,
    solicitarAnulacion,
    handlerChangeText,
    getGuiaSearch,
    showDetails,
    dowloadGuia,
    printGuiaAction,
    motivohandleChange,
    onPressEnter,
    handlerSearch,
    selectUserName,
    setGuiatoAnular,
  ] = UseFormDardeBajaGuias();
  const [printGuia, setprintGuia] = useState(false);

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section">
      <div className="container-fluid pt-4 d-print-none ">
        <h4 className="title-separtor">Rastrear Guía</h4>
        <hr />
        <div className="card card-body">
          {busquedapersonalizada === true ? (
            <></>
          ) : (
            <div className="row align-items-center mb-4">
              <div className="col-sm-12 col-md-6">
                <label htmlFor="simpleinput" className="form-label-input">
                  Buscar Por
                </label>
                <select
                  className="form-select"
                  value={values?.Búsqueda}
                  onChange={handlerChangeText}
                  name="Búsqueda"
                >
                  <option value="guia">Número de Guía</option>
                  <option value="cedula_destinatario">
                    Cédula / RUC Destinatario
                  </option>
                  <option value="cedula_remitente">
                    Cédula / RUC Remitente
                  </option>
                </select>
              </div>
              <div className="col-sm-12 col-md-6">
                <form onSubmit={getGuiaSearch}>
                  <div className="row">
                    <div className="col-sm-12 col-md-6">
                      <label className="form-label-input">Buscar</label>
                    </div>
                    {busquedapersonalizada === true ? (
                      <></>
                    ) : (
                      <div className="col-sm-12 col-md-6 text-start ">
                        <div className="form-check form-switch">
                          Búsqueda Automática
                          <input
                            className="form-check-input"
                            type="checkbox"
                            id="flexSwitchCheckChecked"
                            checked={autosearchBuscarGuias}
                            onChange={onclickSwitch}
                          />
                        </div>
                      </div>
                    )}
                  </div>
                  {/* <label className="form-label-input">Buscar</label> */}
                  <div className="input-group">
                    {autosearchBuscarGuias === true ? (
                      <DebounceInput
                        minLength={0}
                        debounceTimeout={500}
                        type="text"
                        className="form-control dropdown-toggle"
                        placeholder="Buscar ..."
                        id="top-search"
                        name="textSearch"
                        value={values?.textSearch}
                        onChange={(e) => getGuiaSearchAuto(e.target.value)}
                        autoFocus={true}
                      />
                    ) : (
                      <input
                        type="text"
                        className="form-control dropdown-toggle"
                        placeholder="Buscar ..."
                        id="top-search"
                        name="textSearch"
                        value={values?.textSearch}
                        onChange={handlerChangeText}
                        autoFocus={true}
                      />
                    )}
                    <span className="mdi mdi-magnify search-icon" />
                    {fetching ? (
                      <div className="container-spinner-buscar">
                        <Spinner />
                      </div>
                    ) : autosearchBuscarGuias ||
                      busquedapersonalizada === true ? (
                      <></>
                    ) : (
                      <button
                        className="btn-solid-secondary px-4 "
                        type="button"
                        onClickCapture={getGuiaSearch}
                      >
                        Buscar
                      </button>
                    )}
                  </div>
                </form>
              </div>
            </div>
          )}
          <div className="text-start w-20">
            <div className="row">
              <div className="col">
                <a
                  className="nav-link p-2"
                  href={`#pesosvlcrear`}
                  data-bs-toggle="collapse"
                  role="button"
                  aria-expanded="false"
                  aria-controls={`pesosvlcrear`}
                  onClick={setAdvanceSearch}
                >
                  Busqueda Personalizada
                </a>
              </div>
              <div className="col text-end px-4">
                {busquedapersonalizada === true ? (
                  <button
                    type="button"
                    data-bs-toggle="collapse"
                    className="btn-close"
                    onClick={setAdvanceSearch}
                  ></button>
                ) : (
                  <></>
                )}
              </div>
            </div>
          </div>
          {busquedapersonalizada === true ? (
            <div className="align-items-start" id={`pesosvlcrear`}>
              <hr />
              <div className="row align-items-center mb-4">
                {rolUser === "Administrador" ? (
                  <div className="col-sm-12 col-md-3">
                    <div className="input-group">
                      <label className="form-label-input">
                        Nombre de Usuario
                      </label>
                      <div className="input-group-prepend">
                        <span className="input-group-text" id="basic-addon1">
                          {values?.loadSearchUser === true ? (
                            <div className="spinner-border m-5" role="status">
                              <span className="sr-only">Loading...</span>
                            </div>
                          ) : (
                            <i className="material-icons">search</i>
                          )}
                        </span>
                      </div>

                      <DebounceInput
                        minLength={0}
                        debounceTimeout={700}
                        type="search"
                        className="form-control"
                        list="datalistOptions"
                        id="exampleDataList"
                        placeholder="ingresa un Usuario..."
                        value={values?.userName}
                        onChange={handlerSearch}
                        autocomplete="off"
                      />
                    </div>
                    <div className="list-group seter-position">
                      {values?.listFilter?.map((e) => {
                        return (
                          <div
                            onClick={() => selectUserName(e.email)}
                            className=" btn list-group-item list-group-item-action d-flex flex-column text-start"
                          >
                            <b className="text-muted text-capitalize">
                              {removeEmail(e.email)}
                            </b>
                            <span className="rol-text text-capitalize">
                              <span className="text-muted">Rol:</span>{" "}
                              {nameRolUser(e.rol)}
                            </span>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                ) : (
                  <></>
                )}
                <div
                  className={
                    rolUser === "Administrador"
                      ? "col-sm-12 col-md-3"
                      : "col-sm-12 col-md-4"
                  }
                >
                  <label className="form-label-input">
                    Provincia de Origen
                  </label>

                  <select
                    autoComplete={"off"}
                    className="form-select"
                    onChange={handlerChangeText}
                    value={values?.provincia}
                    name="provincia"
                    required
                  >
                    <option key={`#1_destinatario`} value="">
                      Seleccionar una Provincia
                    </option>
                    <option key={`#Todas`} value="todas">
                      TODAS LAS PROVINCIAS
                    </option>
                    {namesProvincias(listProvincias).map((e) => (
                      <option key={`${e}_value`} value={e}>
                        {e}
                      </option>
                    ))}
                  </select>
                </div>
                <div
                  className={
                    rolUser === "Administrador"
                      ? "col-sm-12 col-md-3"
                      : "col-sm-12 col-md-4"
                  }
                >
                  <label className="form-label-input">Fecha Inicio</label>
                  <input
                    type="date"
                    className="form-control"
                    name="startDate"
                    onChange={handlerChangeText}
                    value={values?.startDate}
                  />
                </div>
                <div
                  className={
                    rolUser === "Administrador"
                      ? "col-sm-12 col-md-3"
                      : "col-sm-12 col-md-4"
                  }
                >
                  <label className="form-label-input">Fecha Fin</label>
                  <input
                    type="date"
                    className="form-control"
                    name="endDate"
                    onChange={handlerChangeText}
                    value={values?.endDate}
                  />
                </div>
              </div>
              <div
                role="button"
                onClick={getGuiaSearch}
                className="w-100 btn-solid-secondary p-2"
              >
                Buscar
              </div>
            </div>
          ) : (
            <></>
          )}
        </div>
        <div className="card card-body">
          <div className="col-sm-12 col-md-6">
            <h5 className="titleSection-form">Listado de Guias</h5>
          </div>
          <hr />
          <div className="row align-items-start">
            <div className="row table-cm table-responsive">
              <table className="table  nowrap">
                <thead>
                  <tr className="header-table">
                    <th
                      className="text-nowrap"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="ID"
                    >
                      ID
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Estado"
                    >
                      Estado
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Fecha"
                    >
                      Fecha Creada
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Origen"
                    >
                      Origen
                    </th>
                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Destino"
                    >
                      Destino
                    </th>

                    <th
                      className="text-truncate"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Acciones"
                    >
                      Acciones
                    </th>
                  </tr>
                </thead>

                <tbody>
                  {/* {listTextGuias?.map((guia) => ( */}
                  {listGuias?.map((guia) => {
                    return (
                      <tr key={guia?.idGuia} className="hover-table">
                        <td
                          className="text-nowrap"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={guia?.idGuia}
                        >
                          {guia?.idGuia}
                        </td>
                        <td
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={
                            guia?.estado === null || guia?.estado === ""
                              ? "-----"
                              : guia?.estado
                          }
                        >
                          {guia?.estado === null || guia?.estado === ""
                            ? "-----"
                            : guia?.estado}
                        </td>
                        <td
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={ParseDate(guia?.fechaActual)}
                        >
                          {ParseDate(guia?.creacion?.fecha)}
                        </td>
                        <td
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                        >
                          {`${guia?.remitente?.provincia}-${guia?.remitente?.canton}`}
                        </td>
                        <td
                          className="text-truncate"
                          data-toggle="tooltip"
                          data-placement="top"
                          title={`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                        >
                          {`${guia?.destinatario?.provincia}-${guia?.destinatario?.canton}`}
                        </td>

                        <td>
                          <a
                            className="actions view"
                            type="button"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Ver Detalles"
                            onClick={() => showDetails(true, guia)}
                          >
                            <i className="material-icons">remove_red_eye</i>
                          </a>

                          <a
                            className="actions print"
                            type="button"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Imprimir"
                            onClick={() => printGuiaAction(guia)}
                          >
                            <i className="material-icons">local_printshop</i>
                          </a>

                          {guia?.estado === "creado" ? (
                            <a
                              className="actions anulacion"
                              type="button"
                              data-toggle="tooltip"
                              data-placement="top"
                              title="Anular"
                              data-bs-toggle="modal"
                              data-bs-target="#anularguia"
                              onClick={() => {
                                setGuiatoAnular(guia);
                              }}
                            >
                              <i className="material-icons">block</i>
                            </a>
                          ) : (
                            <></>
                          )}
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>

              <hr />
              {listGuias.length === 0 ? (
                <div>
                  <p>No hay items en la lista</p>
                </div>
              ) : (
                <div>
                  <ReactHTMLTableToExcel
                    className="btn btn-success w-100"
                    table="table-to-excel"
                    filename="Listado de guias"
                    sheet="Guias"
                    buttonText="Descargar Reporte"
                  />
                </div>
              )}
            </div>
          </div>
        </div>

        <h4 className="title-separtor">Detalles</h4>
        <hr />

        {values?.shoDetail ? (
          <div>
            <TableStatus CurrentGuia={currentGuia} />
            <div className="form">
              {currentGuia?.idGuia === undefined ? (
                <></>
              ) : (
                <div className="card card-body">
                  <div className="text-end mb-4">
                    <button
                      type="button"
                      className="btn-close "
                      aria-label="Close"
                      onClick={() => showDetails(false, null)}
                    ></button>
                  </div>
                  {generateGuiatosearch(currentGuia).map((guia) => {
                    return (
                      <EstructuraGuia
                        guia={guia}
                        showLine={!guia?.islast}
                        showSpace={guia?.showSpace}
                        showMarca={false}
                      />
                    );
                  })}
                  {/* <EstructuraGuia
                  guia={CurrentGuia}
                  showLine={false}
                  showSpace={false}
                  showMarca={false}
                /> */}
                </div>
              )}
            </div>
          </div>
        ) : (
          <></>
        )}
        <div
          className="modal fade"
          id="anularguia"
          data-bs-backdrop="static"
          data-bs-keyboard="false"
          aria-labelledby="staticBackdropLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="staticBackdropLabel">
                  Solicitar Anulación de guía
                </h5>
                <button
                  type="button"
                  className="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>

              <div className="modal-body">
                <p>
                  Está seguro de solicitar la anulación de esta guía ?, una vez
                  solicitada no se podra cambiar este estado.
                </p>
                <input
                  type="text"
                  placeholder="Ingrese el motivo de anulación"
                  className="form-control"
                  onChange={motivohandleChange}
                  value={values?.motivoAnulacion}
                  name="motivoAnulacion"
                  required
                />
              </div>

              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-secondary"
                  data-bs-dismiss="modal"
                  onClick={() => {
                    showDetails(false, null);
                  }}
                >
                  Cancelar
                </button>
                <button
                  type="submit"
                  className=" btn btn-danger p-2"
                  data-bs-dismiss="modal"
                  onClick={solicitarAnulacion}
                >
                  Enviar Solicitud
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          className="modal fade"
          id="exampleModal"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">
                  Generar Guía
                </h5>
                <button
                  type="button"
                  className="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <div className="modal-body">...</div>
              <div className="modal-footer">
                <button type="button" className="btn btn-primary">
                  Guardar PDF
                </button>
                <button type="button" className="btn btn-secondary">
                  Imprimir Guias
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Dashfooter />
      {currentGuia?.remitente ? (
        // <div id="guias-export" className="guias-print">
        <div id="guias-export" className="guias-print d-none d-print-block">
          {generateGuiatosearchtoPrint(currentGuia).map((guiaPrint) => {
            return (
              <div className="section-print-guias">
                <EstructuraGuia
                  guia={guiaPrint}
                  showLine={!guiaPrint?.islast}
                  showSpace={guiaPrint?.showSpace}
                  isPrint={true}
                />
              </div>
            );
          })}
        </div>
      ) : (
        <></>
      )}

      {/* =============================== TABLE A EXPORTAR EN EXCEL ============= */}
      {listGuias.length > 0 ? (
        <table className="table" id="table-to-excel">
          <tr className="hover-table">
            <td
              className="text-truncate"
              data-toggle="tooltip"
              data-placement="top"
            ></td>
          </tr>
          <tr className="hover-table">
            <td
              className="text-truncate"
              data-toggle="tooltip"
              data-placement="top"
            >
              Fecha:
            </td>
            <td
              className="text-truncate"
              data-toggle="tooltip"
              data-placement="top"
            >
              {ParseDate(Date.now())}
            </td>
          </tr>
          <tr className="hover-table">
            <td
              className="text-truncate"
              data-toggle="tooltip"
              data-placement="top"
            ></td>
          </tr>
          <tr className="hover-table">
            <td
              className="text-truncate"
              data-toggle="tooltip"
              data-placement="top"
            ></td>
          </tr>
          <thead>
            <tr className="header-table">
              <th>Num Guia</th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Estado"
              >
                Estado
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Fecha"
              >
                Fecha
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Origen"
              >
                Origen (Cantón)
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Destino (Cantón)
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Número de Piezas
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Peso Total
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Tipo
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Valor Asegurado
              </th>
              <th
                className="text-truncate"
                data-toggle="tooltip"
                data-placement="top"
                title="Destino"
              >
                Total
              </th>
            </tr>
          </thead>

          <tbody>
            {/* {listTextGuias?.map((guia) => ( */}
            {listGuias?.map((guia) => {
              return (
                <tr key={guia?.idGuia} className="hover-table">
                  <td>{guia?.idGuia}</td>
                  <td>
                    {guia?.estado === undefined || guia?.estado === ""
                      ? "-----"
                      : guia?.estado}
                  </td>
                  <td>{ParseDate(guia?.creacion?.fecha)}</td>
                  <td>{`${guia?.remitente?.canton}`}</td>
                  <td>{`${guia?.destinatario?.canton}`}</td>
                  <td>{guia?.numPiezas}</td>
                  <td>
                    {returnTotalGuias(guia?.piezas ?? [], guia?.numPiezas)}
                  </td>
                  <td>{guia?.tipo}</td>
                  <td>
                    {returnTotalValorAsegurado(
                      guia?.piezas ?? [],
                      guia?.numPiezas
                    )}
                  </td>
                  <td>0.00</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      ) : (
        <></>
      )}
    </div>
  );
}

export default BuscarGuias;
