import React, { useState } from "react";
import { useSelector } from "react-redux";
import { localidadesList } from "../../../../constants/db_provincias";
import { useFormCreateUser } from "../../../../hooks/use_form_crearUsuario";
import "../crear_usuarios/crear_usuarios.css";

function CrearUsuarios() {
  const {
    auth: { fetching },
  } = useSelector((state) => state);

  const [
    formValues,
    handleInputChange,
    handlerBlur,
    onClickChangeHandler,
    handlerBlurEmail,
    test,
  ] = useFormCreateUser({
    rol: "",
    razonSocial: "",
    ruc: "",
    direccion: "",
    referencia: "",
    provincia: "",
    canton: "",
    parroquia: "",
    responsable: "",
    telefonoEmpresa: "",
    telefonoResponsable: "",
    codigoPostal: "",
    idRemitente: "",
    email: "",
    password: "",
    repassword: "",
    errorMsg: "",
    errorEmail: "",
  });

  const {
    rol,
    razonSocial,
    ruc,
    direccion,
    referencia,
    provincia,
    canton,
    parroquia,
    responsable,
    telefonoEmpresa,
    telefonoResponsable,
    codigoPostal,
    repassword,
    password,
    idRemitente,
    email,
    errorMsg,
    errorEmail,
  } = formValues;

  const [viewPass, setViewPass] = useState(false);
  const visiblePassAction = () => {
    return setViewPass(!viewPass);
  };

  return (
    <div className="crearGuias col-md-9 ms-sm-auto col-lg-10 px-md-4 pt-4 scroll-section">
      <div className="container-fluid pt-4">
        <h4 className="title-separtor">Crear Usuario</h4>
        <hr />
        <div className="card card-body">
          <div className="row">
            <div className="col-sm-12 col-md-6">
              <h5 className="mt-2 titleSection-form">Tipo Usuario</h5>
            </div>
            <div className="col-sm-12 col-md-6">
              <select
                className="col  align-self-start form-select"
                placeholder="Tipo Usuario"
                name="rol"
                value={rol}
                onChange={handleInputChange}
              >
                <option value="">Selecciona tipo usuario</option>
                <option value="admin">Administrador</option>
                <option value="puntoVenta">Puntos de Venta</option>
                <option value="sucursal">Sucursales</option>
                <option value="Corporativo">Clientes Coorporativos</option>
                <option value="cartero">Cartero</option>
              </select>
            </div>
          </div>
          <hr />
          <form onSubmit={onClickChangeHandler}>
            <div className="row ">
              <div className="col-lg-6 ">
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Razón Social
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Nombre de la Empresa, Institución o Local."
                    name="razonSocial"
                    value={razonSocial}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Ruc/Cédula
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Ruc / Cédula / DNI"
                    name="ruc"
                    value={ruc}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Dirección
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Calle principal, e intersección (maximo 70 caracateres)"
                    name="direccion"
                    value={direccion}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Referencia
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Adjunte número de casa o descripción breve del destino"
                    name="referencia"
                    value={referencia}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label-input">Provincia *</label>
                  <select
                    className="form-select"
                    onChange={handleInputChange}
                    value={provincia}
                    name="provincia"
                    required
                  >
                    <option key={`#1_destinatario`} value="">
                      Seleccionar la Provincia
                    </option>
                    {localidadesList.map((e) => (
                      <option
                        key={`${e.provincia}_destinatario`}
                        value={e.provincia}
                      >
                        {e.provincia}
                      </option>
                    ))}
                  </select>
                </div>
                <div className="mb-3">
                  <label className="form-label-input">Cantón *</label>
                  <select
                    className="form-select"
                    name="canton"
                    onChange={handleInputChange}
                    value={canton}
                    required
                  >
                    <option key={`#2_destinatario`} value="">
                      Seleccionar el Cantón
                    </option>

                    {localidadesList
                      .filter((item) => {
                        if (item.provincia === provincia) {
                          return item;
                        }
                      })[0]
                      ?.cantones.map((e) => {
                        return (
                          <option
                            key={`${e.canton}_canton_destinatario`}
                            value={e.canton}
                          >
                            {e.canton}
                          </option>
                        );
                      })}
                  </select>
                </div>
                <div className="mb-3">
                  <label className="form-label-input">Parroquia *</label>
                  <select
                    className="form-select"
                    name="parroquia"
                    onChange={handleInputChange}
                    value={parroquia}
                    required
                  >
                    <option key={`#2_destinatario`} value="">
                      Seleccionar la Parroquia
                    </option>

                    {localidadesList
                      .filter((item) => {
                        if (item.provincia === provincia) {
                          return item;
                        }
                      })[0]
                      ?.cantones.filter((item) => {
                        if (item.canton === canton) {
                          return item;
                        }
                      })[0]
                      ?.parroquias.map((e) => {
                        return (
                          <option key={`${e}_parroquia_destinatario`} value={e}>
                            {e}
                          </option>
                        );
                      })}
                  </select>
                </div>
              </div>

              <div className="col-lg-6 ">
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Responsable
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Nombres Completo"
                    name="responsable"
                    value={responsable}
                    onChange={handleInputChange}
                  />
                </div>

                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Télefono Principal
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Teléfono Principal"
                    name="telefonoEmpresa"
                    value={telefonoEmpresa}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Télefono Secundario
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Teléfono Secundario"
                    value={telefonoResponsable}
                    name="telefonoResponsable"
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Código Postal
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Num. Código Postal"
                    name="codigoPostal"
                    value={codigoPostal}
                    onChange={handleInputChange}
                  />
                </div>
                <div className="mb-3 input-group">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Usuario
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="inputEmail"
                    placeholder="example@example.com"
                    name="email"
                    value={email}
                    onChange={handleInputChange}
                    onBlurCapture={handlerBlurEmail}
                    aria-label="Recipient's username"
                    aria-describedby="basic-addon2"
                  />
                  <div class="input-group-append">
                    <span class="input-group-text" id="basic-addon2">
                      @rapifas.com
                    </span>
                  </div>
                </div>

                <div className="mb-3 input-group">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Contraseña
                  </label>
                  <input
                    type={viewPass === true ? "text" : "password"}
                    className="form-control"
                    placeholder="Contrseña del usuario"
                    name="password"
                    value={password}
                    onChange={handleInputChange}
                    aria-label="Recipient's username"
                    aria-describedby="basic-addon2"
                  />
                  <div className="input-group-append">
                    <span className="input-group-text" id="basic-addon2">
                      <div
                        className="btn-action-view"
                        onClick={visiblePassAction}
                      >
                        {viewPass === true ? (
                          <i className="material-icons">visibility</i>
                        ) : (
                          <i className="material-icons">visibility_off</i>
                        )}
                      </div>
                    </span>
                  </div>
                </div>
                <div className="mb-3 input-group">
                  <label htmlFor="simpleinput" className="form-label-input">
                    Confirmación de Contraseña
                  </label>
                  <input
                    type={viewPass === true ? "text" : "password"}
                    className="form-control"
                    placeholder="Repite la Contrseña"
                    name="repassword"
                    value={repassword}
                    onChange={handleInputChange}
                    onBlur={handlerBlur}
                    aria-label="Recipient's username"
                    aria-describedby="basic-addon2"
                  />
                  <div className="input-group-append">
                    <span className="input-group-text" id="basic-addon2">
                      <div
                        className="btn-action-view"
                        onClick={visiblePassAction}
                      >
                        {viewPass === true ? (
                          <i className="material-icons">visibility</i>
                        ) : (
                          <i className="material-icons">visibility_off</i>
                        )}
                      </div>
                    </span>
                  </div>
                </div>
              </div>
            </div>
            <div className="card card-body">
              <div className="row">
                <button type="button" className=" btn-dt-del col mx-1 p-2 bt-a">
                  Cancelar
                </button>
                <button type="submit" className=" btn-dt-details col mx-1 bt-a">
                  {fetching ? (
                    <div
                      className="spinner-border spinner-border-sm"
                      role="status"
                    >
                      <span className="sr-only">Loading...</span>
                    </div>
                  ) : (
                    "Crear Usuario"
                  )}
                </button>
              </div>
            </div>
          </form>
          {/* <div>
              <button onClick={test}> test</button>
            </div> */}
        </div>
      </div>
    </div>
  );
}

export default CrearUsuarios;
