export const findGuia = (
  tipo,
  num,
  errorSms,
  noEncontrada,
  fechaInicio,
  fechaFin,
  provincia,
  user
) => {
  console.log(user);
  console.log(fechaInicio);
  return async (dispatch) => {
    dispatch({
      type: GET_GUIAS,
    });
    try {
      let listTempGuias = [];
      if (provincia !== undefined) {
        //  //console.log("dentro de personalizada");
        try {
          let query;
          if (provincia === "todas") {
            query = db
              .collection("guias")
              .where("creacion.usuario", "==", `${user.trim()}`);
          } else {
            query = db
              .collection("guias")
              .where("destinatario.provincia", "==", `${provincia}`)
              .where("creacion.usuario", "==", `${user.trim()}`);
          }
          let resp = await query.get().then((doc) => {
            //  //console.log("guia find dentro");
            //  //console.log(doc.docs);
            let listtemp = [];
            doc.docs.forEach((value) => {
              let guia = value.data();

              if (
                guia.creacion.fecha >= fechaInicio &&
                guia.creacion.fecha <= fechaFin + 86400000
              ) {
                listtemp = [...listtemp, value.data()];
              }
            });
            return listtemp;
          });
          if (resp.length === 0) {
            noEncontrada();
          } else {
            listTempGuias = resp;
          }
          //  //console.log(listTempGuias);
          dispatch({
            type: GET_GUIAS_SUCCESS,
            payload: listTempGuias,
          });
        } catch (error) {
          //  //console.log(`error consulta ${error}`);
        }
      } else {
        let query = searchQuery(tipo);
        if (query === "idGuia") {
          let resp = await db
            .collection("guias")
            .doc(`${num}`)
            .get()
            .then((doc) => {
              console.log(doc.data().toString());
              console.log(doc.data());
              return doc.data();
            });
          if (resp === undefined) {
            noEncontrada();
          } else {
            listTempGuias = [...listTempGuias, resp];
          }
        } else {
          let resp = await db
            .collection("guias")
            .where(query, "==", num)
            .get()
            .then((doc) => {
              let guides = [];
              doc.docs.forEach((element) => {
                let result = element.data();
                result.id = element.id;
                guides.push(result);
              });
              return guides;
            });
          if (resp?.length === 0) {
            //  //console.log(resp);
            noEncontrada();
          } else {
            listTempGuias = resp;
          }
        }

        dispatch({
          type: GET_GUIAS_SUCCESS,
          payload: listTempGuias,
        });
      }
    } catch (error) {
      //  //console.log(error);
      dispatch({
        type: GET_GUIAS_ERROR,
        payload: "Error al buscar guías",
      });
      errorSms();

      setTimeout(() => {
        dispatch({
          type: GET_GUIAS_ERROR,
          payload: null,
        });
      }, 6000);
    }
  };
};
//genera el texto de la query a buscar en firebase
const searchQuery = (tipo) => {
  // //  //console.log(`--------------- buscar por ${tipo}-----------`);
  switch (tipo) {
    case "cedula_destinatario":
      return "destinatario.ruc";
    case "cedula_remitente":
      return "remitente.ruc";
    default:
      return "idGuia";
  }
};
export const findLastIDGuia = (success, errorsm) => {
  return async (dispatch) => {
    dispatch({
      type: GET_ID_GUIA,
    });
    try {
      const resp = await db
        .collection("num_guia")
        .doc("1r5LfuzXicxnsN1jj9s8")
        .get()
        .then((doc) => {
          return doc.data();
        });

      dispatch({
        type: GET_ID_GUIA_SUCCESS,
        payload: resp,
      });
      success();
    } catch (error) {
      errorsm();
      dispatch({
        type: GET_ID_GUIA_ERROR,
        payload: `${error}`,
      });

      setTimeout(() => {
        dispatch({
          type: GET_ID_GUIA_ERROR,
          payload: null,
        });
      }, 6000);
    }
  };
};

export const findGuiaPesos = (idGuia, errorMessaje, success) => {
  return async (dispatch) => {
    dispatch({
      type: GET_GUIAS_ACTUALIZAR_PESO,
    });
    try {
      const resp = await db
        .collection("guias")
        .doc(`${idGuia}`)
        .get()
        .then((doc) => {
          return doc.data();
        });
      if (resp == undefined) {
        dispatch({
          type: GET_GUIAS_ACTUALIZAR_ERROR,
          payload: ``,
        });
        errorMessaje("Esta guia no existe");
        document.getElementById("searchGuia").focus();
      } else {
        if (resp?.updatePeso?.length == 2) {
          dispatch({
            type: GET_GUIAS_ACTUALIZAR_ERROR,
            payload: `no es posible agregarla a la lista`,
          });
          errorMessaje(
            "No es posible actualizar el peso de esta guia, ya fue actualizada y modificada"
          );
          document.getElementById("searchGuia").focus();
        } else if (resp.estado === "no entregado") {
          errorMessaje(
            `No es posible actualizar el peso de esta guia. estado: ${resp.estado}`
          );
          document.getElementById("searchGuia").focus();
        } else {
          dispatch({
            type: GET_GUIAS_ACTUALIZAR_SUCCESS,
            payload: resp,
          });
          success();
        }
      }
    } catch (error) {
      dispatch({
        type: GET_GUIAS_ACTUALIZAR_ERROR,
        payload: `Error Al buscar: ${error} `,
      });
      errorMessaje(`Error al buscar: ${error}`);
    }
  };
};

