export const guardarGuias = (
  guias,
  mostrarAlertaOk,
  mostrarAlertaError,
  printguias
) => {
  return async (dispatch) => {
    dispatch({
      type: INSERT_GUIA,
    });

    try {
      let tempListPrint = [];
      for (let guia of guias) {
        let guiaNumDb = await db
          .collection("num_guia")
          // .doc("tBCyvFL2akq8uslE2Cig")
          .doc("1r5LfuzXicxnsN1jj9s8")
          .get()
          .then(async (doc) => {
            console.log("consultando num guia");
            return doc.data();
          });
        guiaNumDb = {
          ...guiaNumDb,
          num_guia: guiaNumDb.num_guia + 1,
        };
        await db
          .collection("num_guia")
          // .doc("tBCyvFL2akq8uslE2Cig")
          .doc("1r5LfuzXicxnsN1jj9s8")
          .update(guiaNumDb);
        let lengDuplicated = 1;
        while (lengDuplicated == 1) {
          let tempCode = generateGuiaNumber(guiaNumDb.num_guia);
          // console.log("Generando Guia");
          let guiasCreadas = await db
            // .collection("guiastest")
            .collection("guias")
            .where("idGuia", "==", tempCode)
            .get()
            .then((doc) => {
              let guides = [];
              doc.docs.forEach((element) => {
                let result = element.data();
                guides.push(result);
              });
              return guides;
            });
          lengDuplicated = guiasCreadas.length;
          if (guiasCreadas.length == 1) {
            // console.log(`---guia ya existe---${tempCode}`);
            guiaNumDb = await db
              .collection("num_guia")
              // .doc("tBCyvFL2akq8uslE2Cig")
              .doc("1r5LfuzXicxnsN1jj9s8")
              .get()
              .then(async (doc) => {
                return doc.data();
              });
            guiaNumDb = {
              ...guiaNumDb,
              num_guia: guiaNumDb.num_guia + 1,
            };
            await db
              .collection("num_guia")
              // .doc("tBCyvFL2akq8uslE2Cig")
              .doc("1r5LfuzXicxnsN1jj9s8")
              .update(guiaNumDb);
          }
        }
        let guiaidCode = generateGuiaNumber(guiaNumDb.num_guia);
        guia.idGuia = guiaidCode;
        console.log(`---- guia creada-----: ${guia.idGuia}`);
        // console.log(`num guia guardar ${guia.idGuia}`);
        // await db.collection("guiastest").doc(`${guia.idGuia}`).set(guia);
        await db.collection("guias").doc(`${guia.idGuia}`).set(guia);
        tempListPrint.push(guia);
      }
      // console.log(tempListPrint);
      dispatch({
        type: SET_PRINT_GUIAS,
        payload: [],
      });
      dispatch({
        type: SET_PRINT_GUIAS,
        payload: tempListPrint,
      });
      dispatch({
        type: SHOW_TO_SALVE,
        payload: true,
      });
      dispatch({
        type: SHOW_TO_SALVE,
        payload: false,
      });
      printguias();
      dispatch({
        type: RESETEAR_ADDGUIAS_CURRENTGUIA,
      });
      mostrarAlertaOk();
    } catch (error) {
      console.log(error);
      dispatch({
        type: INSERT_GUIA_ERROR,
      });
      mostrarAlertaError(`${error}`);
    }
  };
};
