export const actualizarPesoGuias = (
  guias,
  mostrarAlertaOk,
  mostrarAlertaError
) => {
  //  //console.log(guias);
  //  //console.log("dentro del accion");
  return async (dispatch) => {
    dispatch({
      type: UPDATE_PESOS_GUIAS_FIREBASE,
    });
    try {
      guias.map(async (guia) => {
        await db.collection("guias").doc(`${guia.idGuia}`).update(guia);
      });

      dispatch({
        type: UPDATE_PESOS_GUIAS_FIREBASE_SUCCESS,
      });
      mostrarAlertaOk();
    } catch (error) {
      dispatch({
        type: UPDATE_PESOS_GUIAS_FIREBASE_ERROR,
        msgError: error,
      });
      mostrarAlertaError();
    }
  };
};

export const actualizarEstadoGuia = (
  guia,
  mostrarAlertaOk,
  mostrarAlertaError
) => {
  return async (dispatch) => {
    dispatch({
      type: UPDATE_STATUS_GUIA,
    });

    try {
      await db.collection("guias").doc(`${guia.idGuia}`).update(guia);
      dispatch({
        type: UPDATE_STATUS_GUIA_SUCCESS,
      });
      mostrarAlertaOk();
    } catch (error) {
      dispatch({
        type: UPDATE_STATUS_GUIA_ERROR,
      });
      mostrarAlertaError(error);
    }
  };
};

