export const findGuiaAnuladas = async (dispatch) => {
  // //  //console.log("entro a la consulta");
  try {
    const guiasAnuladas = await db
      .collection("guias")
      .where("estado", "==", "pendiente anulacion")
      .get()
      .then((doc) => {
        let guides = [];
        doc.docs.forEach((element) => {
          let result = element.data();
          result.id = element.id;
          guides.push(result);
        });
        return guides;
      });
    // //  //console.log(guiasAnuladas);
    dispatch({
      type: GET_GUIAS_ANULADAS,
      payload: guiasAnuladas,
    });
  } catch (error) {
    // //  //console.log("===> Error al Buscar guía");
    // //  //console.log(error);
    dispatch({
      type: GET_GUIAS_ERROR,
      payload: "Error al buscar guías",
    });
    setTimeout(() => {
      dispatch({
        type: GET_GUIAS_ERROR,
        payload: null,
      });
    }, 6000);
  }
};

