export const getListUsers = (errorSms) => {
  return async (dispatch) => {
    dispatch({
      type: GET_USER_NAME_DB,
    });
    try {
      let listUsersName = [];
      try {
        let resp = await db
          .collection("usuarios")
          .get()
          .then((doc) => {
            if (doc === undefined) {
              return undefined;
            } else {
              let listtemp = [];
              doc.docs.forEach((value) => {
                listtemp = [...listtemp, value.data()];
              });
              return listtemp;
            }
          });
        if (resp === undefined) {
          errorSms("No fue posible realizar la consulta, revisa tu conexión");
        } else if (resp.length === 0) {
          errorSms("No existen usuarios");
        } else {
          listUsersName = resp;
        }
        //  //console.log(listUsersName);
        dispatch({
          type: GET_USER_NAME__DB_SUCCESS,
          payload: listUsersName,
        });
      } catch (error) {
        //console.log(`error consulta ${error}`);
        errorSms(`error consulta usuarios: ${error}`);
        dispatch({
          type: GET_USER_NAME__DB_ERROR,
          payload: null,
        });
      }
    } catch (error) {
      errorSms(`error consulta usuarios: ${error}`);
      dispatch({
        type: GET_USER_NAME__DB_ERROR,
        payload: null,
      });
    }
  };
};

