import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  actualizarEstadoGuia,
  updateitemListDebaja,
  findGuia,
  currentGuiaDetail,
  getListUsers,
  changeBusquedaPesonalizada,
  clearListUserFilter,
  changeautosearchBuscarGuias,
  setListUserFilter,
} from "../redux/actions/guias_actions";

import { useToasts } from "react-toast-notifications";
import {
  convertDate,
  nameRolUser,
  removeEmail,
  removeSpaces,
  savePDF,
} from "../helpers/helper";

export const UseFormDardeBajaGuias = () => {
  const dispatch = useDispatch();
  const {
    guias: {
      listGuias,
      fetching,
      msgError,
      printGuia,
      listusernames,
      listFilterUserNames,
      autosearchBuscarGuias,
      busquedapersonalizada,
    },
    auth: { dataProfile },
  } = useSelector((state) => state);

  const [values, setValues] = useState({
    Búsqueda: "guia",
    textSearch: "",
    shoDetail: false,
    showDownload: false,
    loadSearchUser: false,
    userName: "",
    listFilter: [],
  });
  const rolUser = nameRolUser(dataProfile?.rol);
  const [currentGuia, setCurrentGuia] = useState({});
  const [guiaAnular, setguiaAnular] = useState({});

  //mensajes de alerta
  const { addToast } = useToasts();

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 3000,
    });
  };

  // ------------ serach in put handler ----------------
  const handlerSearch = (e) => {
    setValues({
      ...values,
      userName: e.target.value,
    });
    if (values?.userName !== "") {
      e.preventDefault();
    }
    if (e.target.value.trim()) {
      let tempList = [];
      listusernames.sort().map((user) => {
        if (user.email.startsWith(removeSpaces(e.target.value))) {
          tempList = [...tempList, user];
        }
      });
      if (tempList.length === 0) {
        toastSms("No se encontro este usuario", "info");
      } else {
        setValues({ ...values, listFilter: tempList });
      }
    } else {
      setValues({ ...values, listFilter: [] });
    }
  };
  const selectUserName = (user) => {
    setValues({
      ...values,
      userName: removeEmail(user),
      listFilter: [],
      userNameComplete: user,
    });
  };
  const onclickSwitch = () => {
    dispatch(changeautosearchBuscarGuias(!autosearchBuscarGuias));
  };

  const handlerChangeText = (e) => {
    e?.preventDefault();
    //console.log(e.target.value);
    let res =
      e.target.name === "provincia"
        ? e.target.value
        : e.target.value.replace(/ /g, "");
    setValues({
      ...values,
      [e.target.name]: res.trim(),
    });
  };

  const onPressEnter = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    if (e.key === "Enter") {
      //  //console.log("pres enter");
      // getGuiaSearch();
    }
  };
  const motivohandleChange = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const getGuiaSearch = () => {
    const errorMesajje = () => toastSms(`${msgError}`, "error");
    const noEncontrada = () => toastSms(`Guia no encontrada`, "info");
    if (busquedapersonalizada === true) {
      if (
        values.startDate === undefined ||
        values.endDate === undefined ||
        values.provincia === undefined
      ) {
        toastSms(
          ` Debes ingresar todos los datos para realizar la búsqueda: Fecha Inicial, Fecha Final y  Provincia.`,
          "error"
        );
      } else {
        dispatch(
          findGuia(
            values?.Búsqueda,
            values?.textSearch,
            errorMesajje,
            noEncontrada,
            convertDate(values?.startDate),
            convertDate(values?.endDate),
            values.provincia,
            rolUser === "Administrador"
              ? values?.userNameComplete
              : dataProfile?.email.trim()
          )
        );
      }
    } else {
      if (values?.textSearch.trim()) {
        dispatch(
          findGuia(
            values?.Búsqueda,
            values?.textSearch,
            errorMesajje,
            noEncontrada
          )
        );
      } else {
        toastSms(`El campo de Búsqueda esta vacio`, "error");
      }
    }
  };
  const getGuiaSearchAuto = (idGuia) => {
    //  //console.log(idGuia);
    const errorMesajje = () => toastSms(`${msgError}`, "error");
    const noEncontrada = () => toastSms(`Guia no encontrada`, "info");
    if (idGuia.trim()) {
      dispatch(
        findGuia(
          values?.Búsqueda,
          removeSpaces(idGuia),
          errorMesajje,
          noEncontrada
        )
      );
    } else {
      toastSms(`El campo de Búsqueda esta vacio`, "error");
    }
  };

  const showDetails = (status, guia) => {
    if (guia != null) {
      //  //console.log("contiene guia");
      setValues({
        ...values,
        shoDetail: status,
      });
      setCurrentGuia(guia);
    } else {
      setValues({
        ...values,
        shoDetail: status,
      });
      setCurrentGuia({});
    }
  };

  const darDeBaja = () => {
    const errorMessaje = () => toastSms(`${msgError}`, "error");
    const Success = () => toastSms(`Guia actualizada con exito`, "success");

    let updateStadoGuia = {
      ...currentGuia,
      datosBaja: values,
      estado: "de baja",
    };
    dispatch(actualizarEstadoGuia(updateStadoGuia, Success, errorMessaje));
  };
  const solicitarAnulacion = () => {
    //  //console.log("dentro de solicitar guia");
    const errorMessaje = (errorsms) => toastSms(`${errorsms}`, "error");
    const success = () => toastSms(`Solicitud de anulación enviada`, "success");
    console.log("guia actual");
    console.log(guiaAnular);
    console.log("-------------------------");
    let tempguia = {
      ...guiaAnular,
      estado: "pendiente anulacion",
      anulacion: {
        motivo: values?.motivoAnulacion,
        usuario: dataProfile?.email,
        rol: rolUser?.toString(),
        fecha: Date.now(),
      },
    };
    console.log("antes de enviar");
    console.log(tempguia);
    dispatch(actualizarEstadoGuia(tempguia, success, errorMessaje));
    dispatch(
      updateitemListDebaja(tempguia, tempguia.idGuia, listGuias)
    );
    // showDetails(false, null);
    clearMotivo();
  };

  const clearMotivo = () => {
    setValues({
      ...values,
      Búsqueda: "guia",
      textSearch: "",
      shoDetail: false,
    });
  };

  const dowloadGuia = (guia) => {
    setValues({
      ...values,
      showDownload: true,
    });
    setCurrentGuia(guia);
    //  //console.log(CurrentGuia);
    savePDF("current-guia-salve");
    setTimeout(() => {
      setValues({
        ...values,
        showDownload: false,
      });
    }, 2000);
  };

  const printGuiaAction = (guia) => {
    setCurrentGuia(guia);
    setTimeout(() => {
      window.print();
    }, 500);
  };
  const setGuiatoAnular = (guia) => {
    console.log(guia);
    setguiaAnular(guia);
  };
  //realiza la consulta cuando es admin de usuarios
  const setAdvanceSearch = () => {
    const errorToas = (error) => toastSms(`${error}`, "error");
    dispatch(changeBusquedaPesonalizada(!busquedapersonalizada));
    //console.log(busquedapersonalizada);
    if (autosearchBuscarGuias === true) {
      dispatch(changeautosearchBuscarGuias(false));
    }
    if (
      busquedapersonalizada === false &&
      listusernames.length === 0 &&
      rolUser === "Administrador"
    ) {
      //console.log("----------- consulta realizada -------");
      dispatch(getListUsers(errorToas));
    }
  };

  return [
    values,
    fetching,
    listGuias,
    currentGuia,
    rolUser,
    listusernames,
    listFilterUserNames,
    autosearchBuscarGuias,
    busquedapersonalizada,
    onclickSwitch,
    setAdvanceSearch,
    darDeBaja,
    getGuiaSearchAuto,
    solicitarAnulacion,
    handlerChangeText,
    getGuiaSearch,
    showDetails,
    dowloadGuia,
    printGuiaAction,
    motivohandleChange,
    onPressEnter,
    handlerSearch,
    selectUserName,
    setGuiatoAnular,
  ];
};
