import { useToasts } from "react-toast-notifications";
import { useDispatch, useSelector } from "react-redux";
import { useState, useEffect } from "react";
import { removeSpaces } from "../helpers/helper";
import {
  changeUatoSearchAsignacion,
  clearAsignacionfind,
  findAsignacion,
} from "../redux/actions/manifiestos_actions";
function BuscarAsignacionesHook() {
  const dispatch = useDispatch();
  const { addToast } = useToasts();
  const {
    manifiestos: {
      fetching,
      msgError,
      currentAsignacion,
      autosearchAsignacioncorrespondenciaSearch,
    },
  } = useSelector((state) => state);
  const [values, setValues] = useState({});

  const toastSms = (text, type) => {
    return addToast(text, {
      appearance: type,
      autoDismiss: true,
      autoDismissTimeout: 2000,
    });
  };

  const buscarAsignacionMethod = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    const success = () => toastSms("Asignación encontrada", "success");
    const smserror = (sms) => toastSms(`${sms}`, "error");
    let idAsignacionTemp = removeSpaces(e.target.value);

    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
    if (idAsignacionTemp.includes("`") || idAsignacionTemp.includes("'")) {
      idAsignacionTemp = idAsignacionTemp.replace("`", "-");
      idAsignacionTemp = idAsignacionTemp.replace("'", "-");
    }
    if (idAsignacionTemp.trim()) {
      dispatch(
        findAsignacion(idAsignacionTemp.toUpperCase(), success, smserror)
      );
    } else {
      toastSms("Debes ingresar un Valor", "error");
    }
  };
  const buscarAsignacionMethodManual = () => {
    const success = () => toastSms("Asignación encontrada", "success");
    const smserror = (sms) => toastSms(`${sms}`, "error");
    let idAsignacionTemp = removeSpaces(values.searchMan);
    if (idAsignacionTemp.includes("`") || idAsignacionTemp.includes("'")) {
      idAsignacionTemp = idAsignacionTemp.replace("`", "-");
      idAsignacionTemp = idAsignacionTemp.replace("'", "-");
    }
    if (idAsignacionTemp.trim()) {
      dispatch(
        findAsignacion(idAsignacionTemp.toUpperCase(), success, smserror)
      );
    } else {
      toastSms("Debes ingresar un Valor", "error");
    }
  };

  const autosearchHandler = (e) => {
    if (e.target.value !== "") {
      e.preventDefault();
    }
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const cleanAsignacion = () => {
    return dispatch(clearAsignacionfind());
  };

  const chancheAutoSearch = () => {
    dispatch(
      changeUatoSearchAsignacion(!autosearchAsignacioncorrespondenciaSearch)
    );
  };
  return [
    buscarAsignacionMethod,
    cleanAsignacion,
    chancheAutoSearch,
    values,
    fetching,
    msgError,
    currentAsignacion,
    autosearchAsignacioncorrespondenciaSearch,
    buscarAsignacionMethodManual,
    autosearchHandler,
  ];
}

export default BuscarAsignacionesHook;
